export default {		
	data() {
		return {
			title: 'Hello',
			form:{
				teacher:'苹果老师'
			},
			isLogin:true,
			widths:[10,0],
			width1:0,
			flag:false,
			lineList:[],//默认线路
			shadow:false,
			searchStr:"",
			searchList:[],//搜索出来的线路列表
			CheckNum:0,
			widthList:[0]
		}
	},
	watch:{
		widths:function (oldval,newval){
			console.log(oldval)
		},
		widthList:function (oldval,newval){
			this.widthList=newval
		},
		deep:true
	},
	onLoad() {
		uni.authorize({
			scope:'userInfo'
		})
	},
	onShow(){
		let userInfo=uni.getStorageSync("userInfo")
		if(userInfo){
			this.form=JSON.parse(userInfo)
		}else{
			uni.showToast({
				icon:"none",
				title:"您还没有登录，请先登录!"
			})
			setTimeout(()=>{
				uni.navigateTo({
					url:"../my/login"
				})
			},2000)
		}
		this.getLineQuery()
	},
	methods: {
		getuserinfo(){
			// uni.getUserInfo({
			// 	success:(res)=>{
			// 		console.log(res)
			// 	},
			// 	fail:(err)=>{
			// 		console.log(err)
			// 	}
			// })
			// wx.login({
			// 	success:(res)=>{
			// 		console.log(res)
			// 	}
			// }) 
		},
		goRouter(){
			if(this.isLogin){
				//查看个人资料
				// this.goInfo(2)
				uni.navigateTo({
					url:"../../components/msg/userinfo"
				})
			}else{
				//登陆
				uni.navigateTo({
					url:"../my/login"
				})
			}
			
		},
		lookDetail(){
			console.log("点击")
			this.flag=false
			// this.widths[0]=10
			//运行当中===>线路详情
			uni.navigateTo({
				url:"../../components/msg/linedetails"
			})
		},
		touchStart(e,index){
			console.log("拖动小车")
			this.flag=true
			// console.log(e)
			let page=e.touches[0].pageX
			// console.log(page)
			// console.log(index)
			// switch(index){
			// 	case 0:
			// 	// this.widths[index]=page
			// 	this.width1=page
			// 	// console.log(this.widths[0])
			// }
			this.widthList[index]=page
			
		},
		touchEnd(id){
			if(this.flag){
				console.log("查看线路安全报告")
				uni.navigateTo({
					url:"../../components/msg/security?id="+id
				})
			}
			
		},
		goInfo(index){
			switch(index){
				case 1:
					// 行程记录
					uni.navigateTo({
						url:"../../components/msg/Itineraryrecord"
					})
					break
				case 2:
					this.goRouter()
					break
			}
		},
		// 获取安全员默认的线路
		getLineQuery(){
			this.$http.post("sLine/securityLineQuery",{
				id:this.form.id
			}).then(res=>{
				if(res.code==100){
					this.lineList=res.info
					let list=re.info
					list.forEach((item,index)=>{
						this.widthList[index]=0
					})
				}
			})				
		},
		searchLine(){
			// 搜索线路
			this.$http.post("sLine/lineList",{
				name:this.searchStr,
				id:this.form.id
			}).then(res=>{
				if(res.code==100){
					this.searchList=res.info
				}
			})
		},
		checkLine(id){
			this.CheckNum=id
			// 设置线路默认的安全员、安全员选择线路
			uni.showLoading({
				icon:"loading",
				title:"正在设置"
			})
			this.$http.post("sLine/updateLine",{
				id:this.form.id,
				lineId:id
			}).then(res=>{
				if(res.code==100){						
					setTimeout(()=>{
						uni.hideLoading()
						uni.showToast({
							icon:"success",
							title:"设置成功"
						})
						this.shadow=false
						this.getLineQuery()
					},1000)		
				}
			})
		},
	}
}
